.item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 50px;
  list-style: none;
}
.online {
  margin-right: 10px;
  border-radius: 50%;
  background-color: rgb(81, 214, 76);
  width: 30px;
  height: 30px;
}
.offline {
  margin-right: 10px;
  border-radius: 50%;
  background-color: rgb(214, 76, 76);
  width: 30px;
  height: 30px;
}
